export default {
	data() {
		return {
			mapsId: '',
			scaling:16,
			latitude: '',
			longitude: '',
			covers: [
				{
					latitude: '',
					longitude: '',
					iconPath: 'https://kxg-neituibao-jianli.oss-cn-beijing.aliyuncs.com/data/1582184098427.jpg?Expires=1897544094&OSSAccessKeyId=LTAIfo7xBETxaY0m&Signature=RYs9YSEdkw0AWj9BQitMmVpeXSY%3D'
				}
			]
		};
	},
	onLoad(option) {
		console.log(option.id)
		let that = this;
		uni.request({
			url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/get/channel/dimension/by/id',
			method: 'POST',
			data: {
				id: option.id
			},
			success: res => {
				console.log(res.data);
				that.latitude = res.data.data.sdimension;
				that.longitude = res.data.data.slongitude;
				that.covers[0].latitude = res.data.data.sdimension;
				that.covers[0].longitude = res.data.data.slongitude;
				that.covers[0].iconPath = res.data.data.img;
			},
			fail: () => {
					
			},
		});
	},
	onShow() {
		
	},
	methods: {
		bigger(){
			if(this.scaling<=18){
				this.scaling++
			}else{
				this.scaling=18
			}
		},
		smaller(){
			if(this.scaling>=5){
				this.scaling--
			}else{
				this.scaling=5
			}
			
		},
		getLocation() {
			let that = this;
			uni.getLocation({
				type: 'wgs84',
				success: res => {
					console.log('当前位置的经度：' + res.longitude);
					that.longitude = res.longitude;
					that.latitude = res.latitude;
					console.log('当前位置的纬度：' + res.latitude);
				}
			});
		}
	}
};
